@import '../../variables';

.TrackList {
  margin-top: 30px;
  li {
    padding: 15px 20px;
    background: $color-bg-light;
    border-radius: 5px;
    .row {
      display: flex;
      align-items: center;
      &~div {
        margin-top: 20px; } }
    &:nth-child(odd) {
      background: $color-bg-dark; }
    &.activeTrackItem {
      background: $color-accent;
      .name {
        color: #fff; } } } }

.icon {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $color-accent;
  background: rgba(0, 0, 0, 0.8);
  padding-left: 4px;
  font-size: 28px;
  cursor: pointer; }

.author {
  margin: 0 15px; }

.name {
  color: $text-color; }

.duration {
  font-size: 14px;
  margin-left: 10px; }

.currentTime {
  font-size: 12px;
  margin-right: 10px; }

.heart {
  margin-left: auto;
  font-size: 20px;
  cursor: pointer;
  color: $text-color;
  &.active {
    color: #fff; } }
