@import '../../variables';

.inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-column-gap: 20px;
  margin-top: 20px;
  input {
    border-radius: 30px;
    border: 1px solid $text-color;
    background: transparent;
    width: 100%;
    padding: 5px 16px;
    margin-top: 10px;
    outline: none;
    &:focus {
      border: 1px solid $color-accent; } }
  label {
    margin-bottom: 20px;
    display: block; } }


.AccountSettings {
  width: 100%;
  max-width: 700px;
  margin-left: 50px; }

.errorMessage {
  color: red;
  font-size: 12px; }
