@import '../../variables';

.Search {
  display: flex;
  input {
    margin-right: 10px;
    border-radius: 30px;
    border: 1px solid $text-color;
    width: 280px;
    background: transparent;
    padding: 5px 20px;
    font-size: 12px;
    font-weight: 500; }
  button {
    background: $color-accent;
    color: #fff;
    border-radius: 50%;
    padding: 5px 7px; } }

